	module.exports = {"data":{"markdownRemark":{"html":"<p>\"Distorted People\"を読んでください。物語は、一般的な、元の性的な感情や騒ぎの少し慎重な記述、西洋語で書かれたスタイルは非常に興味深いですユーモアとユーモアの不足はありませんが、時には説明されるオブジェクトがあまりにも貧しい、ほとんど普通の農家や田舎の幹部です。私はこれが事実ではないとは言いませんが、実際にそうであるとは言いません。</p>\n<p>オリエンタルリサーチ・インスティテュートでは、把握すべき課題について議論しました。問題は、道徳性の高い人が矛盾し、スマートでない人は矛盾の側にあるということです。今の主な問題は、1994年の安定性の維持方法です。今年の主な問題は、金融、ビジネス、農業です。農村問題はもっと心配です。</p>\n<p>東アジア研究所は、マクロ経済の規制・管理の仕組み、主に金融のバランスと不均衡、民主主義の中央集権、集中と民主主義を含む政府の管理は十分ではないので、発生してはならない問題が多く、農業、農業の問題は今年の自然条件に問題を抱えており、管理を中心に問題を抱える人もいるし、香港の問題や社会イデオロギーもある。</p>\n<p>今年、東洋研究所の研究思想について誰もが議論し、決定しています。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0033.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0033.md","frontmatter":{"title":"2月16日 水曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0033.md absPath of file >>> MarkdownRemark"}}

